.banner-swiper {
  position: relative;
  overflow: hidden;
  border-radius: 0;
  margin-top: 0.46875rem;
  max-height: 26.09375rem;
}
.banner-swiper .carousel {
  overflow: hidden;
}
.banner-swiper .carousel-inner {
  max-height: 26.25rem;
  min-height: 9.375rem;
}
.banner-swiper .carousel-item {
  max-height: 26.25rem;
}
.banner-swiper .swiper-container {
  width: 100%;
  height: 13.125rem;
}
.banner-swiper .swiper-wrapper {
  width: 100%;
  height: 13.125rem;
}
.banner-swiper .swiper-wrapper .adm-swiper-slide {
  width: 100%;
  height: 13.125rem;
  overflow: hidden;
}
.banner-swiper .swiper-wrapper .adm-swiper-indicator .adm-page-indicator-dot {
  width: 1.5625rem;
}
